import { useEffect, useState } from "react"
import style from "./Chat.module.css"
import ChatFriendItem from "./ChatFriendItem";

const ChatFriendMenu = ({userData, myUid, setSelectUser}) => {

    const [friendIDList, setFriendIDList] = useState([]);

    useEffect(() => {
        if(userData) {
            for(let data in userData.val()[myUid]["Friend"]) {
                setFriendIDList(prevList => [...prevList, userData.val()[myUid]["Friend"][data]])
            }
        }
    }, [userData])

    useEffect(() => {
        console.log(friendIDList)
    }, [friendIDList])

    return (
        <div className={style.ChatFriendMenuContainer}>
            <p></p>
            {friendIDList.map((value, index) => {

                const FriendUserName = userData.val()[value]["UserName"]

                return (
                    <ChatFriendItem key={index} FriendUserName={FriendUserName} FriendUid={value} setSelectUser={setSelectUser}/>
                )
            })}
        </div>
    )
}

export default ChatFriendMenu;