import style from "./Chat.module.css"

const ChatFriendItem = ({ FriendUserName, FriendUid, setSelectUser }) => {

    const UserSelect = () => {
        setSelectUser(FriendUid)
    }

    return (
        <div className={style.FriendListContainer}>
            <div className={style.UserContainer} onClick={UserSelect}>
                <div className={style.UserIcon} />
                <p className={style.FriendUserName}>{FriendUserName}</p>
            </div>
            <div className={style.FriendItemBorder} />
        </div>
    )
}

export default ChatFriendItem;