import style from "./Friend.module.css"
import { get, ref } from "firebase/database";
import { database } from "../../firebase";
import { useEffect, useState } from "react";

const FriendItem = ({ itemUid, selectUser, setSelectUser }) => {

    const dbUserRef = ref(database, "/User")
    const [userName, setUserName] = useState("")

    const getUser = async () => {
        const dbUser = await get(dbUserRef);
        setUserName(dbUser.val()[itemUid]["UserName"])
    }

    const select = () => {
        setSelectUser(itemUid)
    }


    useEffect(() => {
        getUser();
    }, [])

    return(
        <div className={style.FriendItemContainer}>
            <div className={style.UserNameContainer} onClick={select}>
                <div className={style.UserImage} />
                <p className={style.UserName}>{userName}</p>
            </div>
                <div className={style.FriendItemBorder} />
        </div>
    )
}

export default FriendItem;