import { useState } from "react";
import style from "./Menu.module.css";
import MenuButton from "./MenuButton";
import MenuItem from "./MenuItem";

const Menu = () => {

    const [MenuToggle, setMenuToggle] = useState(false);

    return (
        <div className={style.Menu}>
            <div className={`${style.FillBackground} ${MenuToggle ? style.ActiveBackground : style.PassiveBackground}`}></div>
            <div className={`${style.MenuContainer} ${MenuToggle ? style.ActiveMenu : style.PassiveMenu}`}>
                <MenuItem title="Home" navlink="/" />
                <MenuItem title="Chat" navlink="/chat" />
                <MenuItem title="Friend" navlink="/friendrequest" />
                <MenuItem title="Logout" navlink="/login" />
            </div>
            <MenuButton MenuToggle={MenuToggle} setMenuToggle={setMenuToggle} />
        </div>
    )
}

export default Menu;