import style from "./Menu.module.css";

const MenuButton = ({ MenuToggle, setMenuToggle }) => {

    const ButtonClick = () => {
        setMenuToggle(!MenuToggle)
    }

    return (
        <div className={style.MenuButtonContainer}>
            <input type="button" className={style.MenuButton} onClick={ButtonClick}/>
        </div>
    )
}

export default MenuButton;