import { get, ref } from "firebase/database";
import style from "./Friend.module.css"
import { database } from "../../firebase";
import { useEffect, useRef, useState } from "react";
import FriendSearch from "./FriendSearch";

const RequestSend = () => {

    const myUid = localStorage.getItem("loginUser")
    const dbFriendID = ref(database, `User/${myUid}/FriendID`)
    const [MyFriendID, setMyFriendID] = useState("");
    const SearchFriendID = useRef();

    useEffect(() => {
        const fetchFriendID = async () => {
            const getID = await get(dbFriendID)
            setMyFriendID(getID.val())
        }
        
        fetchFriendID();
    }, [dbFriendID]);

    const Search = () => {
        FriendSearch(SearchFriendID, myUid)
    }

    return (
        <div className={style.RequestSendContainer}>
            <p className={style.IDContext}>ID{MyFriendID}</p>
            <div style={{display: "flex", justifyContent: "center", alignItems: "center"}}>
                <input type="text" className={style.IDInputBox} placeholder="Friend ID" ref={SearchFriendID}/>
                <input type="button" value={""} className={style.SendButton} onClick={Search}/>
            </div>
        </div>
    )
}

export default RequestSend;