import style from "./Login.module.css"

const LSButton = ({ LSToggle, onClick }) => {
    return (
        <div className={style.ButtonContainer}>
            <input type="button" value={LSToggle ? "Sign In" : "Sign Up"} className={`${style.LSButton} ${LSToggle ? style.LoginButton : style.SignUpButton}`} onClick={onClick}/>
        </div>
    )
}

export default LSButton;