import style from "./Chat.module.css"
import Menu from "../Menu/Menu";
import ChatFriendMenu from "./ChatFriendMenu";
import ChatBox from "./ChatBox";
import { get, ref } from "firebase/database";
import { database } from "../../firebase";
import { useEffect, useState } from "react";


const Chat = () => {

    const [userData, setUserData] = useState();
    const [selectUser, setSelectUser] = useState();
    const myUid = localStorage.getItem("loginUser")
    const dbUserRef = ref(database, "/User")

    const getUserData = async () => {
        const dbUser = await get(dbUserRef)
        setUserData(dbUser);
    }

    useEffect(() => {
        getUserData()
    }, [])

    return(
        <div>
            <Menu />
            <div className={style.Chat}>
                <ChatFriendMenu userData={userData} myUid={myUid} selectUser={selectUser} setSelectUser={setSelectUser} />
                <ChatBox userData={userData} selectUser={selectUser} myUid={myUid} />
            </div>
        </div>
    )
}

export default Chat;