import { useState } from "react";
import style from "./Friend.module.css"
import RequestSend from "./RequestSend";
import RequestSet from "./RequestSet";
import FriendItem from "./FriendItem";

const Request = ({ selectUser, setSelectUser }) => {

    const  [requestList, setRequestList] = useState([]);
    const myUid = localStorage.getItem("loginUser")


    RequestSet(myUid, requestList, setRequestList)

    return (
        <div className={style.RequestContainer}>
            <div className={style.RequestList}>
                <p className={style.FriendRequestTitle}></p>
                <div className={style.requestContainer}>
                    {requestList.map((value, index) => {
                        return(
                            <FriendItem key={index} itemUid={value} selectUser={selectUser} setSelectUser={setSelectUser}/>
                        )
                    })}
                </div>
                <RequestSend />
            </div>
        </div>
    )
}

export default Request;