import { createUserWithEmailAndPassword } from "firebase/auth";
import { auth, database } from "../../firebase"
import { ref, set } from "firebase/database";
import IDCreator from "./IDCreator";

const signUpAuth = async (userName, email, password, passwordCheck, navigate) => {

    if(userName && email && password && passwordCheck) {
        if(password === passwordCheck) {
            try {
                const userCredential = await createUserWithEmailAndPassword(auth, email, password);
                localStorage.setItem("loginUser", userCredential.user.uid);

                const dbUserName = ref(database, `User/${userCredential.user.uid}/UserName`)
                set(dbUserName, userName)

                const dbFriendID = ref(database, `User/${userCredential.user.uid}/FriendID`)
                const newID = IDCreator();
                set(dbFriendID, newID)


                navigate("/")

            } catch (error) {
                console.log("")
            }
        } else {
            console.log("")
        }
    } else {
        console.log("")
    }

};

export default signUpAuth;