import { onChildAdded, ref } from "firebase/database";
import { database } from "../../firebase";
import { useEffect } from "react";

const RequestSet = (myUid, requestList, setRequestList) => {
    useEffect(() => {
        const dbRequest = ref(database, `User/${myUid}/Request`)
        onChildAdded(dbRequest, (data) => {
            setRequestList(prevList => [...prevList, data.val()])
        })
    }, [])
}

export default RequestSet;