import style from"./Login.module.css"

const LSInput = ({ LSToggle, userNameRef, emailRef, passwordRef, passwordCheckRef }) => {
    return (
        <div className={style.InputContainer}>
            <input type="text" className={!LSToggle ? style.Input : style.PassiveInput } placeholder="Username" readOnly={LSToggle} ref={userNameRef}/>
            <p className={style.ErrorMessage}></p>
            <input type="text" className={style.Input} placeholder="Email" ref={emailRef}/>
            <p className={style.ErrorMessage}></p>
            <input type="password" className={style.Input} placeholder="Password" ref={passwordRef}/>
            <p className={style.ErrorMessage}></p>
            <input type="password" className={!LSToggle ? style.Input : style.PassiveInput } placeholder="Password (Check)" readOnly={LSToggle} ref={passwordCheckRef}/>
        </div>
    )
}

export default LSInput;