import { onChildAdded, ref } from "firebase/database";
import { useEffect, useState, } from "react";
import { database } from "../../firebase";

const GetMessage = (selectUser, myUid, setDisplayMessage) => {
    
    const [messageList, setMessageList] = useState([]);
    const dbMyChat = ref(database, `/User/${myUid}/Chat/${selectUser}`)
    const dbFriendChat = ref(database, `/User/${selectUser}/Chat/${myUid}`)

    useEffect(() => {
        if(selectUser) {
            onChildAdded(dbMyChat, (MessageRef) => {
                setMessageList((prevMessage) => [...prevMessage, MessageRef.val()])
            })
            onChildAdded(dbFriendChat, (MessageRef) => {
                setMessageList((prevMessage) => [...prevMessage, MessageRef.val()])
            })

        }
    }, [selectUser])

    useEffect(() => {
        const sortMessage = messageList.sort((a, b) => {
            if(a[2] > b[2]) {
                return 1; 
            } else {
                return-1
            }
        })

        setDisplayMessage(sortMessage)
    }, [messageList])

}

export default GetMessage;