import { get, push, ref, remove, set } from "firebase/database";
import style from "./Friend.module.css"
import { database } from "../../firebase";
import { useEffect, useState } from "react";

const RequestDetail = ({ selectUser }) => {

    const dbUserRef = ref(database, "/User");
    const [userData, setUserData] = useState();
    const [displayUserName, setDisplayUserName] = useState("");
    const myUid = localStorage.getItem("loginUser")

    const getUser = async () => {
        const dbUser = await get(dbUserRef);
        setUserData(dbUser);
    }

    const Accept = () => {

        if(selectUser !== "") {
            const dbMyFriend = ref(database, `/User/${myUid}/Friend`)
            const dbMyFriendpush = push(dbMyFriend)
            const dbFriendFriend = ref(database, `/User/${selectUser}/Friend`)
            const dbFriendFriendpush = push(dbFriendFriend)

            set(dbMyFriendpush, selectUser)
            set(dbFriendFriendpush, myUid)

            for(let data in userData.val()[myUid]["Request"]) {
                if(userData.val()[myUid]["Request"][data] == selectUser) {
                    
                    const dbDeleteRef = ref(database, `User/${myUid}/Request/${data}`)
                    remove(dbDeleteRef)
                    window.location.reload();
                }
            }
        }
    }

    const Reject = () => {
        for(let data in userData.val()[myUid]["Request"]) {
            if(userData.val()[myUid]["Request"][data] == selectUser) {
                
                const dbDeleteRef = ref(database, `User/${myUid}/Request/${data}`)
                remove(dbDeleteRef)
                window.location.reload();
            }
        }
    }


    useEffect(() => {
        if(userData) {
            setDisplayUserName(userData.val()[selectUser]["UserName"])
        }
    }, [selectUser])

    useEffect(() => {
        getUser()
    }, [])


    return (
        <div className={style.RequestDetailContainer}>
            <div className={style.RequestDetail}>
                <p className={style.displayUserName}>{displayUserName}</p>
                <div className={style.buttonBox}>
                    <input type="button" value={""} className={style.Button} onClick={Accept}/>
                    <input type="button" value={""} className={style.Button2} onClick={Reject}/>
                </div>
            </div>
        </div>
    )
}

export default RequestDetail;