import { useEffect, useRef, useState } from "react";
import style from "./Chat.module.css"
import { push, ref, set } from "firebase/database";
import { database } from "../../firebase";
import GetMessage from "./GetMessage";
import MessageItem from "./MessageItem";

const ChatBox = ({ userData, selectUser, myUid }) => {

    const inputText = useRef("");
    const dbFriendChatRef = ref(database, `/User/${selectUser}/Chat/${myUid}`)
    const [selectFriend, setSelectFriend] = useState("");
    const [displayMessage, setDisplayMessage] = useState([]);

    const send = () => {
        const sendMessage = inputText.current.value
        const dbFriendChat = push(dbFriendChatRef);
        const d = new Date();
        const date = String(d)
        const datems = d.getTime()
        if(sendMessage !== "") {
            set(dbFriendChat, [sendMessage, date, datems, myUid])
            inputText.current.value = ""
        }
    }
    
    GetMessage(selectUser, myUid, setDisplayMessage);

    useEffect(() => {
        if(userData) {
            const userName = userData.val()[selectUser]["UserName"]
            setSelectFriend(userName)
        }
    }, [selectUser])

    return (
        <div className={style.ChatBoxContainer}>
            <p>{selectFriend}</p>
            <div className={style.ChatBox}>
                {
                    displayMessage.map((value, index) => {
                        return(
                            <MessageItem key={index} Message={value[0]} Time={value[1]} className={ value[3] == myUid ? style.MyMessage : style.OtherMessage}/>
                        )
                    })
                }
            </div>
            <div className={style.InputContainer}>
                <input type="text" className={style.InputText} ref={inputText}/>
                <input type="button" value={""} className={style.SendButton} onClick={send}/>
            </div>
        </div>
    )
}

export default ChatBox;