import { get, push, ref, set } from "firebase/database";
import { database } from "../../firebase";

const FriendSearch = (FriendID, myUid) => {

    const dbUserRef = ref(database, "/User");

    (async () => {
        const dbUser = await get(dbUserRef);
        for(let uid in dbUser.val()) {
            if(dbUser.val()[uid]["FriendID"] === FriendID.current.value) {
                const dbRequestRef = ref(database, `/User/${uid}/Request`)
                const dbRequest = push(dbRequestRef)
                set(dbRequest, myUid)
                alert(`${dbUser.val()[uid]["UserName"]}`)
                FriendID.current.value =""
            }
        }
    })();

}

export default FriendSearch;
