import style from "./Login.module.css"

const LSSwitch = ({ LSToggle, setLSToggle }) => {

const SwitchToggle = () => {
    setLSToggle(!LSToggle)
}

    return (
        <div className={style.SwitchContainer}>
            <p className={LSToggle ? style.Active : style.Passive}>Sign In</p>
            <div className={style.Switch} onClick={SwitchToggle}>
                <div className={`${style.SwitchCircle} ${LSToggle ? style.LoginSwitch : style.SignUpSwitch}`} ></div>
            </div>
            <p className={!LSToggle ? style.Active : style.Passive}>Sign Up</p>
        </div>
    )
}

export default LSSwitch;