import { useRef, useState } from "react";
import style from "./Login.module.css"
import LSSwitch from "./LSSwitch";
import LSInput from "./LSInput";
import LSButton from "./LSButton";
import loginAuth from "./LoginScript";
import signUpAuth from "./SignUpScript";
import { useNavigate } from "react-router-dom";


const Login = () => {

    const [LSToggle, setLSToggle] = useState(true)
    const userNameRef = useRef();
    const emailRef = useRef();
    const passwordRef = useRef();
    const passwordCheckRef = useRef();

    const navigate = useNavigate();

    const LSButtonClick = () => {

        const userName = userNameRef.current.value
        const email = emailRef.current.value
        const password = passwordRef.current.value
        const passwordCheck = passwordCheckRef.current.value


        LSToggle ? loginAuth(email, password, navigate) : signUpAuth(userName, email, password, passwordCheck, navigate)
    }

    return (
        <div className={style.MainContainer}>
            <div className={style.LoginContainer}>
                <LSSwitch LSToggle={LSToggle} setLSToggle={setLSToggle} />
                <LSInput LSToggle={LSToggle} userNameRef={userNameRef} emailRef={emailRef} passwordRef={passwordRef} passwordCheckRef={passwordCheckRef}/>
                <LSButton LSToggle={LSToggle} onClick={LSButtonClick} />
            </div>
        </div>
    )
};

export default Login;